.entrence{
    width: 100vw;
    height: 100vh;
    background-color: #3d3d3d;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.align{
    width: 100vw;
    height: 60vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    animation: reversBlink 4s;
}
.align .boxs {
    width: 25vw;
    height: 60vh;
    
    display: flex;
    flex-direction: column;
}
.align .boxs .box1{
    flex: .5 1;
    background-color: #252827;
    transform: translateY(15vh);
    
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    transition: .5s;
}
.align .boxs .box2{
    flex: .5 1;
    
    transform: translateY(-15vh);
    z-index: 1;
    transition: .5s;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}

.align .boxs:hover .box1{
    transform: translateY(0);
    transition: .5s;
}
.align .boxs:hover .box2{
    transform: translateY(0);
    transition: .5s;
}
.align .boxs .box2 h1{
    color: black;
    margin: 2vw;
    font-size: 1.5vw;
}
.box1 img{
    width: 25vw;

}
.box2 a{
    position: absolute;
    bottom: 1vw;
    right: 1vw;
    padding: .4vw;
    background-color: white;
    color: #f98909;
    border-radius: 25px;
    text-decoration: none;
}
.box2 h4{
    font-size: 1.1vw;
    color: black;
}

.logo{
    position: absolute;
    left: 10vw;

}
.logo img{
    width: 80vw;
    animation: blink 2s;
    opacity: 0;
}
@keyframes blink{
    from{
        opacity: 1;
    }to{
        opacity: 0;
    }
}
@keyframes reversBlink{
    0%{
        opacity: 0;
    }
    50%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.bg_yellow{
    background-color: #f5d136;
}
.bg_green{
    background-color: #aceefd;
}
.bg_orange{
    background-color: #f98909;
}
.bs_yellow{
    box-shadow: 0 5px 5px 0 #f5d136;

}
.bs_green{
    box-shadow: 0 5px 5px 0 #aceefd;

}
.bs_orange{
    box-shadow: 0 5px 5px 0 #f98909;

}
.top{
    position: absolute;
    top: 0;
    left: 40vw;
}
.top img{
    width: 20vw;
}
.mobil_align{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    animation: reversBlink 4s;
}.mobil_align .boxs {
    width: 60vw;
    height: 25vh;
    background-color: #252827;
    display: flex;
    flex-direction: column;
    cursor: pointer;z-index: 2;
}
.mobil_align .boxs img{
    margin: 0;
    padding: 0;
    width: 60vw;
    height: 25vh;
    cursor: pointer;
}
.mobile_top{
    position: absolute;
    top: 0;
    right: 0;
}.mobile_top img{
    width: 25vw;
}
