.mobil_nav_pc{
  position: relative;
  color: #f98909 ;
  width: 100%;

}
.mobil_nav_pc h2{
  display: none;
}
.mobil_nav_pc.active h2{
  display: flex;
}
.mobil_nav_pc .burger{
  width: 10vw;
  height: 10vw;
  position: relative;
  float: right;
  margin: auto;
  display: inline;
  border: 1px solid white;
  margin-right: 1vw;
  margin-top: 1vw;
  border-radius: 1vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.mobil_nav_pc .burger .line{
  border-bottom: 1px solid white;
  width: 8vw;
  height: 1vw;
  transition: .5s;
}
.mobil_nav_pc .burger.act .line:nth-child(1){
  transform: rotateZ(45deg) translateY(1.5vw) translateX(2vw);
  transition: .5s;
}
.mobil_nav_pc .burger.act .line:nth-child(2){
  opacity: 0;
  transition: .5s;
}
.mobil_nav_pc .burger.act .line:nth-child(3){
  transform: rotateZ(135deg) translateY(1.5vw) translateX(-2vw);
  transition: .5s;
}
